e responsabilidad del departamento de distribución y producción, después del proceso de producción el cual es la fase final, se procede a realizar el cortado de páginas, inclusión de material publicitario, los pliegues y finalmente el encuadernado, pegado de etiquetas y embalaje, después de todo este proceso todo el material (ejemplares) se apilan de acuerdo con su destino, se cargan en estibas para facilitar su traslado a los vehículos encargados de su entrega final.")])]),_c('div',{staticClass:"col-md-6"},[_c('figure',[_c('img',{staticClass:"contenacord",attrs:{"src":require("@/assets/curso/tema1/acor6.png"),"alt":"Texto que describa la imagen"}})])])])]),_c('SlyderC',{staticClass:"mb-5",attrs:{"datos":_vm.datosSlyder}}),_c('Separador'),_vm._m(22),_vm._m(23),_vm._m(24),_c('p',{staticClass:"mt-5"},[_vm._v("Dentro del proceso productivo existen áreas encargadas de tareas importantes, con el fin de obtener un buen contenido en todo aspecto, dichas áreas están divididas de la siguiente manera:")]),_c('p',[_vm._v("Consejo de redacción, investigación, redacción, edición, diagramación, impresión, distribución.")]),_c('p',{staticClass:"mb-5"},[_vm._v("Estas se diferencian así:")]),_c('div',{staticClass:"row mb-5"},[_c('div',{staticClass:"col-md-1"}),_c('div',{staticClass:"col-md-10"},[_c('LineaTiempoD',{staticClass:"color-acento-botones"},[_c('p',{staticClass:"text-small",attrs:{"numero":"1","titulo":"Consejo de redacción:"}},[_vm._v("la creación de un consejo editorial es la primera etapa para crear un periódico u otro medio impreso. Aquí es donde se reúnen los órganos más importantes del medio, director, redactor jefe, editores de las secciones individuales, comité editorial. La tarea principal del equipo editorial es determinar qué noticias e información se publicarán, qué enfoque se propone de acuerdo con un tema determinado.")]),_c('p',{staticClass:"text-small",attrs:{"numero":"2","titulo":"Investigación:"}},[_vm._v("en esta etapa de la estructura de un periódico, los periodistas son primordiales, de igual forma que los fotógrafos e ilustradores, en el proceso de investigación su principal herramienta es ubicar las fuentes, las cuales pueden ser directas o indirectas, y donde se recoge la mayor cantidad posible de información para escribir noticias, reportajes, crónicas , la actividad principal de los fotógrafos e ilustradores en el escenario investigativo es lograr el material e imágenes que soporten y complementen los textos.")]),_c('p',{staticClass:"text-small",attrs:{"numero":"3","titulo":"Redacción:"}},[_vm._v("es el escenario donde todo periodista escribe una noticia, crónica, reportaje, columna, es una etapa de trabajo individual en el que el periódico debe estar provisto de títulos y subtítulos textuales, así como de cada una de sus secciones claramente definidas, dependerá del editor de cada sección la jerarquía asociada con el mensaje o la información.")]),_c('p',{staticClass:"text-small",attrs:{"numero":"4","titulo":"Edición:"}},[_vm._v("en esta etapa del proceso los editores son los encargados de editar cada sección, ya que son ellos quienes conocen cada tema y pueden corroborar la veracidad de la información, después de esta acción el contenido se le entrega al director general, quien hace una última revisión y aprueba definitivamente los textos, en esta etapa se ajustan y determinan los textos del periódico. Es en esta fase se anexan puntos, tildes, signos de interrogación etc, se elimina información, se corroboran los datos y se estandariza la información sin perder el estilo que todo periodista ha impreso en ella.")]),_c('p',{staticClass:"text-small",attrs:{"numero":"5","titulo":"Diagramación:"}},[_vm._v("esta etapa tiende ser compleja, puesto que implica que todo contenido sea fácil de leer, que la estructura del texto sea correcta y proporcionada, que las imágenes sean comprensibles y coherentes con el texto o la información ya que son el apoyo, el responsable de esto es el departamento de diseño. Al crear la 